@font-face {
    font-family: "Segoe UI";
    font-style: normal;
    font-weight: 400;
    src: url("../fonts/SegoeUI.woff2") format("woff2"),
    url("../fonts/SegoeUI.woff") format("woff");
    font-display: swap;
}

@font-face {
  font-family: "Segoe UI";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/SegoeUI-Bold.woff2") format("woff2"),
  url("../fonts/SegoeUI-Bold.woff") format("woff");
  font-display: swap;
}
